import { printLine } from './modules/print';

var duration;
var pageHasVideo;
var video;

var startTime;
var pausedTime;
var endTime;

window.onload = function () {

  // Listener for metadata of video
  chrome.runtime.onMessage.addListener(
      function(message, sender, sendResponse) {
        video = document.getElementsByTagName("video")[0];
        if (video) {
          // video exists on the page
          duration = video.duration;
          pageHasVideo = duration != null ? true : false;
        } else {
          pageHasVideo = false;
          //TODO: if page doesn't have video, show dialogue
        }

          switch(message.type) {
              case "pageHasVideo":
                  sendResponse(pageHasVideo);
                  break;
              case "getDuration":
                  sendResponse(duration);
                  break;
              default:
                  console.error("Unrecognised message: ", message);
          }
      }
  );

  function loop() {
    if(this.currentTime >= endTime) {
        // loop
        video.currentTime = startTime;
    }
  }

  // Listener to control the video
  chrome.runtime.onMessage.addListener(
      function(message, sender, sendResponse) {
          var success = true; // may be false cases in the future

          switch(message.type) {
              case "startLoop":
                  startTime = parseFloat(message.startTime);
                  endTime = parseFloat(message.endTime);

                  // Add listener to video
                  video.addEventListener("timeupdate", loop);
                  // Set speed
                  video.playbackRate = parseFloat(message.speed);
                  // start video at startTime
                  sendResponse(success);
                  break;
              case "pauseLoop":
                  // pause video
                  video.pause();
                  pausedTime = video.currentTime;
                  video.removeEventListener("timeupdate", loop);
                  sendResponse(success);
                  break;
              case "resumeLoop":
                  video.currentTime = pausedTime;
                  video.addEventListener("timeupdate", loop);
                  video.play();
              case "stopLoop":
                  // pause video
                  video.pause();
                  // remove video listener
                  video.removeEventListener("timeupdate", loop);
                  sendResponse(success);
                  break;
              case "changeSpeed":
                  // change video speed
                  video.playbackRate = message.speed;
                  sendResponse(success);
                  break;
              case "changeStartTime":
                  // change startTime
                  startTime = message.startTime;
                  sendResponse(success);
                  break;
              case "changeEndTime":
                  // change endTime
                  endTime = message.endTime;
                  sendResponse(success);
                  break;
              default:
                  console.error("Unrecognised message: ", message);
          }
      }
  );


}.bind(this);
